import React, {Component} from 'react';
import Navbar from './navbar/Navbar';
import Main from './main/Main';
export default class App extends Component{
    constructor(props){
        super(props);
        this.state={
            svgUrl:'',
            text:''
        }
    }

    selectedElement(data){
       this.setState({
        svgUrl:data.svgUrl,
        text:data.text
       })
    }
    render(){
        return(
            <div className="container-fluid custom_container_90 col-12 col-lg-10 ">
               <Navbar selectedElement={this.selectedElement.bind(this)}  />
               <Main data={this.state}/>
            </div>
        )
    }
}